/*
=====
LEVEL 1. CORE STYLES
=====
*/

.toggle{
    --uiToggleSize: var(--toggleSize, 20px);
    --uiToggleIndent: var(--toggleIndent, 1em);
    --uiToggleBorderWidth: var(--toggleBorderWidth, 1px);
    --uiToggleColor: var(--toggleColor, #000);
    /*--uiToggleDisabledColor: var(--toggleDisabledColor, #868e96);*/
    --uiToggleBgColor: var(--toggleBgColor, #fff);
    --uiToggleArrowWidth: var(--toggleArrowWidth, 2px);
    --uiToggleArrowColor: var(--toggleArrowColor, #fff);

    display: inline-block;
    position: relative;
    margin: 30px 0 0;
}

.toggle__input{
    position: absolute;
    left: -99999px;
}

.toggle__label{
    color: var(--uiToggleColor);
    display: inline-flex;
    cursor: pointer;
    min-height: var(--uiToggleSize);
    padding-left: calc(var(--uiToggleSize) + var(--uiToggleIndent));
}

.toggle__label:before, .toggle__label:after{
    content: "";
    box-sizing: border-box;
    width: 1em;
    height: 1em;
    font-size: var(--uiToggleSize);

    position: absolute;
    left: 0;
    top: 0;
}

.toggle__label:before{
    border: var(--uiToggleBorderWidth) solid var(--uiToggleColor);
    z-index: 2;
}

/*.toggle__input:disabled ~ .toggle__label:before{*/
/*    border-color: var(--uiToggleDisabledColor);*/
/*}*/

.toggle__input:focus ~ .toggle__label:before{
    box-shadow: 0 0 0 1px var(--uiToggleBgColor), 0 0 0 2px var(--uiToggleColor);
}

.toggle__input:not(:disabled):checked:focus ~ .toggle__label:after{
    box-shadow: 0 0 0 1px var(--uiToggleBgColor), 0 0 0 2px var(--uiToggleColor);
}

.toggle__input:not(:disabled) ~ .toggle__label:after{
    background-color: var(--uiToggleColor);
    opacity: 0;
}

.toggle__input:not(:disabled):checked ~ .toggle__label:after{
    opacity: 1;
}

.toggle__text{
    margin-top: auto;
    margin-bottom: auto;
}

/*
The arrow size and position depends from sizes of square because I needed an arrow correct positioning from the top left corner of the element toggle
*/

.toggle__text:before{
    content: "";
    box-sizing: border-box;
    width: 0;
    height: 0;
    font-size: var(--uiToggleSize);

    border-left-width: 0;
    border-bottom-width: 0;
    border-left-style: solid;
    border-bottom-style: solid;
    border-color: var(--uiToggleArrowColor);

    position: absolute;
    top: .5428em;
    left: .2em;
    z-index: 3;

    -webkit-transform-origin: left top;

            transform-origin: left top;
    -webkit-transform: rotate(-40deg) skew(10deg);
            transform: rotate(-40deg) skew(10deg);
}

.toggle__input:not(:disabled):checked ~ .toggle__label .toggle__text:before{
    width: .5em;
    height: .25em;
    border-left-width: var(--uiToggleArrowWidth);
    border-bottom-width: var(--uiToggleArrowWidth);
    will-change: width, height;
    -webkit-transition: width .1s ease-out .2s, height .2s ease-out;
    transition: width .1s ease-out .2s, height .2s ease-out;
}

/*
=====
LEVEL 2. PRESENTATION STYLES
=====
*/

/*
The demo skin
*/

.toggle__label:before, .toggle__label:after{
    border-radius: 2px;
}

/*
The animation of switching states
*/

.toggle__input:not(:disabled) ~ .toggle__label:before,
.toggle__input:not(:disabled) ~ .toggle__label:after{
    opacity: 1;
    -webkit-transform-origin: center center;
            transform-origin: center center;
    will-change: transform;
    -webkit-transition: -webkit-transform .2s ease-out;
    transition: -webkit-transform .2s ease-out;
    transition: transform .2s ease-out;
    transition: transform .2s ease-out, -webkit-transform .2s ease-out;
}

.toggle__input:not(:disabled) ~ .toggle__label:before{
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    -webkit-transition-delay: .2s;
            transition-delay: .2s;
}

.toggle__input:not(:disabled) ~ .toggle__label:after{
    -webkit-transform: rotateY(90deg);
            transform: rotateY(90deg);
}

.toggle__input:not(:disabled):checked ~ .toggle__label:before{
    -webkit-transform: rotateY(-90deg);
            transform: rotateY(-90deg);
    -webkit-transition-delay: 0s;
            transition-delay: 0s;
}

.toggle__input:not(:disabled):checked ~ .toggle__label:after{
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
    -webkit-transition-delay: .2s;
            transition-delay: .2s;
}

.toggle__text:before{
    opacity: 0;
}

.toggle__input:not(:disabled):checked ~ .toggle__label .toggle__text:before{
    opacity: 1;
    -webkit-transition: opacity .1s ease-out .3s, width .1s ease-out .5s, height .2s ease-out .3s;
    transition: opacity .1s ease-out .3s, width .1s ease-out .5s, height .2s ease-out .3s;
}

/*
=====
LEVEL 3. SETTINGS
=====
*/

.toggle{
    --toggleColor: #538039; /*#690e90;*/
    --toggleBgColor: #b2f28d; /*#9b59b6;*/
    --toggleSize: 1.5em;
    --toggleIndent: 0.8em;
    --toggleBorderWidth: 2px;
    --toggleArrowWidth: 2px;
    --toggleArrowColor: #fff;
}

.mm-popup {
    display: none;
}

.mm-popup--visible {
    display: block;
}

.mm-popup__overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    overflow: auto;
    background: rgba(0, 0, 0, .1);
}

.mm-popup__close {
    position: absolute;
    top: 15px;
    right: 20px;
    padding: 0;
    width: 20px;
    height: 20px;
    cursor: pointer;
    outline: none;
    text-align: center;
    border-radius: 10px;
    border: none;
    text-indent: -9999px;
    background: transparent url('data:image/png;base64,') no-repeat center center;
    background-size: 100%;
    margin: 0;
}

.mm-popup__input {
    display: block;
    width: 100%;
    height: 30px;
    border-radius: 3px;
    background: #f5f5f5;
    border: 1px solid #e9ebec;
    outline: none;
    box-sizing: border-box !important;
    font-size: 14px;
    padding: 0 12px;
    color: #808080;
}

.mm-popup__btn {
    border-radius: 3px;
    box-sizing: border-box;
    padding: 0 10px;
    margin: 0;
    line-height: 32px;
    height: 32px;
    border: 1px solid #666;
    text-align: center;
    display: inline-block;
    font-size: 12px;
    font-weight: 400;
    color: #333;
    background: transparent;
    outline: none;
    text-decoration: none;
    cursor: pointer;
    font-family: "Roboto", sans-serif;
}

.mm-popup__btn--success {
    background-color: #27ae60;
    border-color: #27ae60;
    color: #fff;
}

.mm-popup__btn--danger {
    background-color: #c5545c;
    border-color: #c5545c;
    color: #fff;
}

.mm-popup__box {
    width: 350px;
    position: fixed;
    top: 10%;
    left: 50%;
    margin-left: -175px;
    background: #fff;
    box-shadow: 0 5px 20px 0 rgba(126,137,140,0.20);
    border-radius: 5px;
    border: 1px solid #B8C8CC;
    overflow: hidden;
    z-index: 1001;
}

.mm-popup__box__header {
    padding: 15px 20px;
    background: #EDF5F7;
    color: #454B4D;
}

.mm-popup__box__header__title {
    margin: 0;
    font-size: 16px;
    text-align: left;
    font-weight: 600;
}

.mm-popup__box__body {
    padding: 20px;
    line-height: 1.4;
    font-size: 14px;
    color: #454B4D;
    background: #fff;
    position: relative;
    z-index: 2;
}

.mm-popup__box__body p {
    margin: 0 0 5px;
}

.mm-popup__box__footer {
    overflow: hidden;
    padding: 40px 20px 20px;
}

.mm-popup__box__footer__right-space {
    float: right;
}

.mm-popup__box__footer__right-space .mm-popup__btn {
    margin-left: 5px;
}

.mm-popup__box__footer__left-space {
    float: left;
}

.mm-popup__box__footer__left-space .mm-popup__btn {
    margin-right: 5px;
}

.mm-popup__box--popover {
    width: 300px;
    margin-left: -150px;
}

.mm-popup__box--popover .mm-popup__close {
    position: absolute;
    top: 5px;
    right: 5px;
    padding: 0;
    width: 20px;
    height: 20px;
    cursor: pointer;
    outline: none;
    text-align: center;
    border-radius: 10px;
    border: none;
    text-indent: -9999px;
    background: transparent url('data:image/png;base64,') no-repeat center center;
    background-size: 100%;
    margin: 0;
    z-index: 3;
}

.mm-popup__box--popover .mm-popup__box__body {
    padding: 20px;
}

@media (max-width: 420px) {
    .mm-popup__box {
        width: auto;
        left: 10px;
        right: 10px;
        top: 10px;
        margin-left: 0;
    }

    .mm-popup__box__footer__left-space {
        float: none;
    }

    .mm-popup__box__footer__right-space {
        float: none;
    }

    .mm-popup__box__footer {
        padding-top: 30px;
    }

    .mm-popup__box__footer .mm-popup__btn {
        display: block;
        width: 100%;
        text-align: center;
        margin-top: 10px;
    }
}
