.enter-page {
    width: 90%;
    padding: 8% 0 0;
    margin: auto;
    text-align: center;
}
.enter-main-region {
    position: relative;
    z-index: 1;
    background: #FFFFFF;
    max-width: 360px;
    margin: 0 auto 100px;
    padding: 45px;
    /*text-align: center;*/
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.enter-main-region input {
    font-family: "Roboto", sans-serif;
    outline: 0;
    background: #f2f2f2;
    width: 6%;
    border: 0;
    /*margin: 0 0 15px;*/
    padding: 15px;
    /*box-sizing: border-box;*/
    /*font-size: 14px;*/
    vertical-align: middle;
}
